import React, { Component,useState } from "react";
import 'react-bulma-components/dist/react-bulma-components.min.css';
import { Button } from 'react-bulma-components';
import Background from '../src/Wallpaper.png';


const HeroComponent = () => {
  

    
    const [notSearched, searchDone] = useState(false);

    if(notSearched=== true){
        return(
            <div className="section">
                <div className="notification">
                <input className="input is-rounded" type="text" placeholder="This will be what the user searches"></input>
                <br></br><br></br>
                <table className="table is-striped is-hoverable is-fullwidth">
                    
                    
                        <tr>
                            
                            <th>Film Name</th>
                            <th>Description</th>
                            <th>Platform</th>
                            
                        </tr>
                        
                        <tbody>
                            <tr>
                                <td>John Wick</td>
                                <td>John's dog gets killed by a Russian fella, John murders all Russians as a result.</td>
                                <td>Netflix</td>
                            </tr>
                            <tr>
                                <td>UP</td>
                                <td>Old man with a young boy.</td>
                                <td>Netflix</td>
                            </tr>
                            <tr>
                                <td>Grand Tour</td>
                                <td>Three old men and cars.</td>
                                <td>Amazone Prime Video</td>
                            </tr>
                        </tbody>
                    

                </table>
                </div>
            </div>


        );
    }
     else{
        const heroStyle = {
        backgroundImage: `url(${Background})`,
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',   
      };
        return (<section style={heroStyle} className="hero is-fullheight">
        <div className="hero-body">
            <div className="container">
                <h1 className="title has-text-white">
                <input className="input is-rounded" type="text" placeholder="Type a movie or TV show you would like to watch"></input>
                <button onClick={() => searchDone(true)} className="button is-dark">Submit</button>

                </h1>
            </div>
        </div>
    </section>

        )}};
export default HeroComponent; 




